<!-- html -->
<template>
  <div style="width: 70%;margin: 0px auto;">
    <div style="text-align: left;margin-bottom: 25px;">
      <span>&nbsp;&nbsp;</span>
      <el-input style='width: 200px;' v-model="cname" placeholder="" clearable></el-input>&nbsp;&nbsp;
      <el-button @click='search'></el-button>
      <!-- <el-button @click='add'></el-button> -->
    </div>
    <h1 style="margin-bottom: 40px; margin-top: 15px;"></h1>
    <el-table :data="dataTask" border="">
      <template slot="empty">
      	<span v-if="isshow"></span>
      	<span v-if="!isshow"></span>
      </template>
      <el-table-column align='center' prop="number" label="" ></el-table-column>
      <el-table-column align='center' prop="name" label="" width="130%"></el-table-column>
      <el-table-column align='center' prop="process" label="" width="190%"></el-table-column>
      <el-table-column align='center' prop="subTaskCount" label=""></el-table-column>
      <el-table-column align='center' prop="taskTime" label=""></el-table-column>
      <el-table-column align='center' prop="intro" label="" width="400%"></el-table-column>
      <el-table-column align='center' v-if="dataTask.length!=0" width="100%">
      	<template slot-scope="scope">
      		<el-button size='small' type="text" @click='zrw(scope.row)'></el-button>
      	</template>
      </el-table-column>
    </el-table>
    <h1 style="margin-top: 40px;"></h1>
    <el-table :data="dataSubTask" border="" style="margin-top: 40px;margin-bottom: 40px;">
      <template slot="empty">
      	<span v-if="isshow2"></span>
      	<span v-if="!isshow2"></span>
      </template>
      <el-table-column align='center' prop="number" label="" width="91%" :resizable="false"></el-table-column>
      <el-table-column align='center' prop="type" label="" width="91%" :resizable="false"></el-table-column>
      <el-table-column align='center' prop="name" label="" width="140%" :resizable="false"></el-table-column>
      <el-table-column align='center' prop="name1" label="" :resizable="false"></el-table-column>
      <el-table-column align='center' prop="content" label="" width="350%" :resizable="false"></el-table-column>
      <!-- <el-table-column align='center' prop="contentType" label="">
        <template slot-scope="scope">
        	<span v-if="scope.row.contentType==0"></span>
        	<span v-if="scope.row.contentType==1"></span>
        </template>
      </el-table-column> -->
      <el-table-column align='center' prop="role" label="" :resizable="false"></el-table-column>
      <el-table-column align='center' prop="keywords" label="" width="200%" :resizable="false"></el-table-column>
      <el-table-column align='center' prop="acThreshold" label="" width="95%" :resizable="false"></el-table-column>
      <el-table-column align='center' prop="answer" label="" :resizable="false"></el-table-column>
    </el-table>

  </div>
</template>
<!-- js -->
<script>
  export default {
    data() {
      return {
        cname: '',
        list: [],
        data: [],
        dataTask: [],
        dataSubTask : [],
        isshow:false,
        isshow2:false
      };
    },
    methods: {
      search() {
        this.dataSubTask = new Array
        this.dataTask = new Array
        var data =  {
            keyWord:null,
            page:0,
            size:0
        }
        this.$global.httpPostaddTask(this, '/api/v1/task/queryTasks', data).then(res => {
            this.data = res.data;
            var a = 1
            //
            for(var i = 0; i < this.data.length; i++){
              //
              // if(this.cname == ""){
              //   var dataTask = this.data[i].tasks
              //   if(dataTask!=undefined){
              //     for (var j = 0; j < dataTask.length; j++) {
              //       dataTask[j].number = a++
              //       this.dataTask.push(dataTask[j])
              //     }
              //   }
              // //
              // }else
              if(this.data[i].name === this.cname&&this.cname!=""){
                var dataTask = this.data[i].tasks
                if(dataTask!=undefined){
                  for (var j = 0; j < dataTask.length; j++) {
                    dataTask[j].number = a++
                    this.dataTask.push(dataTask[j])
                  }
                }
              }
            }
            this.isshow=true
        })
      },
      add(){
      	//
        this.$store.commit('xiaoh')
      	this.$router.push({path:'/index5'});
      },
      //
      zrw(row){
        this.dataSubTask = new Array
        for (var i = 0; i < row.subTasks.length; i++) {
          this.dataSubTask.push(row.subTasks[i])
          this.dataSubTask[i].name1 = row.name
          this.dataSubTask[i].number = row.number+"."+(i+1)
        }
        this.isshow2=true
      },
    },
    mounted: function() {
      // this.search();
    }
  }
</script>
<!-- css -->
<style>
  .el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
</style>



